import React, { useState} from 'react';
import { Link } from 'react-router-dom'; 
import logo from '../images/Logo.svg';

export default function Header(props) {

    const [isDataOpen, setIsDataOpen] = useState(false);

    function openData () {
        setIsDataOpen(true)
    }

    function closeData () {
        setIsDataOpen(false)
    }

    return (
    <header className="header">
        <div className="header__size">
            <div className={`header__burger ${isDataOpen ? 'active' : ''}`}>
                <p className="header__text" >{props.email}</p>
                <Link
                    to="/sign-in"
                    className="header__link_mobile"
                    onClick={props.onOut}>
                    –í—ã–π—Ç–∏
                </Link>                
            </div>
        </div>
        <div className="header__container">
            <img src={logo} alt="–õ–æ–≥–æ" className="header__logo"/>
            {props.loggedIn &&
            <> 
                <div className={`menu__icon ${isDataOpen ? 'close' : ''}`} onClick={openData}>
                    <span></span>
                </div>
                <div className="header__size_close-icon">
                    <button className={`header__close-icon ${isDataOpen ? 'active' : ''}`} type="button" aria-label="–∑–∞–∫—Ä—ã—Ç—å" onClick={closeData}></button>
                </div>
            </>
            }
            <div className="header__box">
                {props.loggedIn ? (
                    <>
                        <p className="header__text" >{props.email}</p>
                        <Link
                        to="/sign-in"
                        className="header__link"
                        onClick={props.onOut}
                        >
                        –í—ã–π—Ç–∏
                        </Link>
                    </>
                ) : ( 
                <>
                    {(props.currentRoute === '/sign-in') ? (
                    <Link
                        to="/sign-up"
                        className="header__link"
                    >
                        –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
                    </Link>
                    ) : (
                    <Link
                        to="/sign-in"
                        className="header__link"
                    >
                        –í–æ–π—Ç–∏
                    </Link>
                    )}
                </>
                )}
            </div>
        </div>
    </header>
)
}