import React, { useState,  useEffect  } from 'react';
import { CurrentUserContext } from '../contexts/CurrentUserContext.js';
import { Route, Routes, useNavigate } from 'react-router-dom';
import Header  from "./Header.js";
import Footer from "./Footer.js";
import Main from './Main.js';
import api  from '../utils/Api.js';
import Login from './Login.js';
import Register from './Register.js';
import InfoTooltip from './InfoTooltip.js';
import ProtectedRoute from './ProtectedRoute.js';
import auth from '../utils/Auth.js';

import ImagePopup  from './ImagePopup.js';
import DeleteCardPopup from './DeleteCardPopup.js';
import EditProfilePopup from './EditProfilePopup.js';
import EditAvatarPopup from './EditAvatarPopup.js'
import AddPlacePopup from './AddPlacePopup.js';


export default function App() {

    const navigate = useNavigate();

    //—Å—Ç–µ–π—Ç—ã popups
    const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = useState(false);
    const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = useState(false);
    const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = useState(false);
    const [isImagePopupOpen, setImagePopupOpen] = useState(false);
    const [isDeleteCardPopupOpen, setIsDeleteCardPopupOpen] = useState(false);
    const [isInfoTooltip, setIsInfoTooltip] = useState(false);

    const [selectedCard, setSelectedCard] = useState({});
    const [currentUser, setCurrentUser] = useState({});
    const [cards, setCards] = useState([]);
    
    const [currentEmail, setCurrentEmail] = useState('');
    const [successRegister, setSuccessRegister] = useState(false);

    // —Å—Ç–µ–π—Ç –º–∞—Ä—à—Ä—É—Ç–æ–≤
    const [currentRoute, setCurrentRoute] = useState('');

    //—Å—Ç–µ–π—Ç –ª–æ–≥–∏–Ω–∞
    const [loggedIn, setLoggedIn] = useState(false);

    // –∑–∞–ø—Ä–æ—Å  –¥–∞–Ω–Ω—ã—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    useEffect(() => {
        api.getUserInfo()
            .then((userInfo) => {
                setCurrentUser(userInfo);
            })
            .catch((err) => {
                console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
            });
    }, []);

    //–∑–∞–ø—Ä–æ—Å–∞ –º–∞—Å—Å–∏–≤–∞ –¥–∞–Ω–Ω—ã—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
    useEffect(() => {
        api.getInitialCards()
            .then((cardsInfo) => {
                setCards(cardsInfo);
            })
            .catch((err) => {
                console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
            });
    }, []);
    
    // –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ –æ—Ç–∫—Ä—ã—Ç—è –≤—Å–µ—Ö popup
    const handleEditAvatarClick = () => {
        setIsEditAvatarPopupOpen(true);
    }

    const handleAddPlaceClick = () => {
        setIsAddPlacePopupOpen(true);
    }

    const handleEditProfileClick = () => {
        setIsEditProfilePopupOpen(true);
    }

    const handleDeleteCardClick = () => {
        setIsDeleteCardPopupOpen(true);
    }

    const handleRegisterClick = () => {
        setIsInfoTooltip(true);
    }

    const handleCardClick = (data) => {
        setImagePopupOpen(true);
        setSelectedCard(data);
    }

    // –∑–∞–∫—Ä—ã—Ç–∏–µ –≤—Å–µ—Ö popup
    const closeAllPopups = () => {
        setIsEditAvatarPopupOpen(false);
        setIsAddPlacePopupOpen(false);
        setIsEditProfilePopupOpen(false);
        setImagePopupOpen(false);
        setIsDeleteCardPopupOpen(false);
        setIsInfoTooltip(false);
    }

    //–≤—ã—Ö–æ–¥
    function handleOutput() {
        if (localStorage.getItem('token')) {
            localStorage.removeItem('token');
            navigate('/sign-in');
            setLoggedIn(false);
        }
    }

    //—É–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –ª–∞–π–∫–∞–º–∏
    function handleCardLike(card) {
        const isLiked = card.likes.some((i) => i._id === currentUser._id);
    
        api.changeLikeCardStatus(card._id, !isLiked)
            .then((newCard) => {
                setCards((state) =>
                state.map((c) => (c._id === card._id ? newCard : c))
            );
        })
            .catch((err) => {
                console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
            });
    }

    //—É–¥–∞–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç—ã
    function handleCardDelete(card) {
        api.deleteCard(card._id)
            .then(() => {
            setCards((state) => 
            state.filter((c) => c._id !== card._id));
            })
            .catch((err) => {
            console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
            });
    }

    //—Å–º–µ–Ω–∞ –¥–∞–Ω–Ω—ã—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    function handleUpdateUser(data) {
        api.setUserInfo(data)
            .then((userInfo) => {
                setCurrentUser(userInfo);
                closeAllPopups();
            })
            .catch((err) => {
                console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
            });
    }

    //—Å–º–µ–Ω–∞ –∞–≤–∞—Ç–∞—Ä–∞
    function handleUpdateAvatar(data) {
        api.addAvatar(data)
            .then((userAvatar) => {
            setCurrentUser(userAvatar);
            closeAllPopups();
            })
            .catch((err) => {
                console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
            });
    }
    
    //–¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–π –∫–∞—Ä—Ç—ã
    function handleAddCard(element) {
        api.addCard(element)
                .then((newCard) => {
                    setCards([newCard, ...cards]);
                    closeAllPopups();
                })
                .catch((err) => {
                    console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
                });
    }
    // –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ –∑–∞–∫—Ä—ã—Ç–∏—è
    function handleClosePopup(evt) {
        if (
            evt.target.classList.contains('popup')
            || evt.target.classList.contains('popup__close-icon')
        ) {
            closeAllPopups();
        }
    }

    useEffect(() => {
        function handleEscClose(evt) {
            if (evt.keyCode === 27) closeAllPopups();
        }
    
        document.addEventListener('keydown', handleEscClose);
    
        return () => document.removeEventListener('keydown', handleEscClose);
    }, []);

    // —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è
    function handleRegister({ registerData, setRegisterData }) {
        auth.register(registerData)
            .then(() => {
                setSuccessRegister(true);
                setIsInfoTooltip(true);
                navigate('/sign-in');
    
            setRegisterData({
                email: '',
                password: '',
            });
        })
            .catch((err) => {
                setSuccessRegister(false);
                setIsInfoTooltip(true);
    
            
            
                console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
            });
    }

    // –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è
    function handleAuthorize({ loginData, setLoginData }) {
        auth.authorize(loginData)
            .then((data) => {
                if (data.token) {
                    setLoggedIn(true)
                    navigate('/')
                    localStorage.setItem('token', data.token);
    
                    setLoginData({
                        email: '',
                        password: '',
                    });
                }
            })
            
            .catch((err) => {
                console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
            });
    }

    useEffect(() => {
        function handleTokenCheck() {
            if (localStorage.getItem('token')) {
            const token = localStorage.getItem('token');
            auth.checkToken(token)
                .then((data) => {
                    if (data) {
                        setCurrentEmail(data.data.email);
                        setLoggedIn(true);
                        navigate('/');
                    }
                })
                .catch((err) => {
                    console.log(`–í–Ω–∏–º–∞–Ω–∏–µ! ${err}`);
                });
        }
    }

    handleTokenCheck();
}, []);




return (
<div className="page">
    <CurrentUserContext.Provider value={currentUser}>
        <Header 
            currentRoute={currentRoute}
            loggedIn={loggedIn}
            email={currentEmail}
            onOut={handleOutput}
        />
        <Routes>
            <Route exact path='/'  element={
                <ProtectedRoute loggedIn={loggedIn}>
                    <Main
                        onEditAvatar={handleEditAvatarClick}
                        onAddPlace={handleAddPlaceClick}
                        onEditProfile={handleEditProfileClick}
                        onCardClick={handleCardClick}
                        cards={cards}
                        onCardLike={handleCardLike}
                        onCardDelete={handleCardDelete}
                    />
                </ProtectedRoute>    
                }
            />
            <Route exact path="/sign-up" element={
                <Register
                    setCurrentRoute={setCurrentRoute}
                    onRegister={handleRegister}
                    onInfoTooltip={handleRegisterClick}
                    successRegister={successRegister}
                />
            } /> 
            <Route exact path="/sign-in" element={
                <Login 
                    setCurrentRoute={setCurrentRoute}
                    onLogin={handleAuthorize}
                    navigate={navigate}
                    loggedIn={loggedIn}

                />
            } />
        </Routes>
        {loggedIn && <Footer />}       
        <ImagePopup 
            data={selectedCard}
            isOpen={isImagePopupOpen}
            onClose={handleClosePopup}
        />
        <EditAvatarPopup 
            isOpen={isEditAvatarPopupOpen}
            onClose={handleClosePopup}
            onUpdateAvatar={handleUpdateAvatar}
        />
        <EditProfilePopup 
            isOpen={isEditProfilePopupOpen} 
            onClose={handleClosePopup} 
            onUpdateUser={handleUpdateUser}
            />
        <AddPlacePopup 
            isOpen={isAddPlacePopupOpen} 
            onClose={handleClosePopup} 
            onAddCard={handleAddCard}
            />     
        <DeleteCardPopup
            isOpen={isDeleteCardPopupOpen} 
            onClose={handleClosePopup} 
        />
        <InfoTooltip
            name="registration"
            isOpen={isInfoTooltip} 
            onClose={handleClosePopup}
            registration–°ompleted={successRegister} 
        />
    </CurrentUserContext.Provider>
</div>
);
}
    