import React from 'react';
import successfully from '../images/successfully.svg'
import unsuccessfully from '../images/unsuccessfully.svg'

export default function InfoTooltip (props) {

    return (
        <div className={`popup popup_type_${props.name} ${props.isOpen ? 'popup_opened' : ''}`}  onClick={props.onClose}>
            <div className="popup__container">
                <button className="popup__close-icon link-aim" type="button" aria-label="–∑–∞–∫—Ä—ã—Ç—å" onClick={props.onClose}></button>
                <div className="popup__box">
                    <img
                        src={props.registration–°ompleted ? successfully : unsuccessfully}
                        alt={props.registration–°ompleted ? "–≥–∞–ª–æ—á–∫–∞" : "–∫—Ä–µ—Å—Ç–∏–∫"}
                        className="popup__icon"
                    />
                    <p className="popup__text">
                        {props.registration–°ompleted
                        ? '–í—ã —É—Å–ø–µ—à–Ω–æ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª–∏—Å—å!'
                        : '–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.'}
                    </p>
                </div>
            </div> 
        </div>
    )
}
